import React from 'react';
import I from 'immutable';
import { connect } from 'react-redux';

class SecondPage extends React.PureComponent {
  addTodo = () => {
    let updatedTodoList =  this.props.data;
    const todoObj = {
      id: new Date().toString(),
      text: this.props.todo
    };

    updatedTodoList.push(todoObj);
    this.props.setStoreSecondPage(updatedTodoList);
    this.props.changeTodo('');
  }
  removeTodo = (id) => {
    let removedTodo = this.props.data;
    
    const a = removedTodo.filter((item) => item.id !== id);
    console.log(a)
    this.props.setStoreSecondPage(a);
    this.props.changeTodo('');
  }
  changeTodo = (e) => {
    this.props.changeTodo(e.target.value);
  }
  render() {
    const { todo, data } = this.props;

    return (
      <div>
        <input type="text" placeholder="Todo" onChange={ this.changeTodo }></input>
        <button disabled={ todo.text === '' } onClick={ this.addTodo }>{"add todo"}</button>
        <div>
          { 
            data.map(item => <div key={item.id}><li>{ item.text }</li><button onClick={ () => this.removeTodo(item.id) }>{"X"}</button></div>)
          }
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const secondPage = state.get('secondPage', I.Map());

  return {
    data: secondPage.get('data', []),
    todo: secondPage.get('todo', { id: '', text: ''}),
  };
};

const mapDispatchToProps = dispatch => ({
  setStoreSecondPage: data => dispatch({ type: 'SET_STORE_SECOND_PAGE', data }),
  changeTodo: todo => dispatch({ type: 'SET_STORE_TODO', todo})
});

export default connect(mapStateToProps, mapDispatchToProps)(SecondPage);